.contacts__text {
  font-weight: normal;
  font-size: 14px;
  line-height: 180%;
  /* or 25px */
  color: #787878;
  text-decoration: none;

  &:hover {
    color: #71A7E2;
  }

  &:focus {
    border: 1px solid #71A7E2;
    box-sizing: border-box;
  }
  &:active{
    color: #0E3B6C;
  }
}
.contacts__content{
  max-width: 220px;
}
