.page {
  margin: 0 0;
  padding: 0 0;
  box-sizing: border-box;
  font-family: "Open Sans", sans-serif;
  font-style: normal;
}

.h2 {
  font-weight: 300;
  font-size: 36px;
  line-height: 130%;
  /* identical to box height, or 47px */
  color: #1C1C1C;
}
.line{
  border-bottom: 1px solid #E5E5E5;
}
