.form-join-us {
  display: grid;
  grid-template-columns: 250px 250px 250px 250px;
  gap: 19px;
  justify-content: center;
  @media (max-width:1200px) {
    grid-template-columns: 25% 25% 25% 25%;
    margin: 0 100px;
  }
  @media (max-width: 950px) {
    grid-template-columns: 50% 50%;
    margin: 0 50px;
  }
}

.form-join-us__left {
  max-width: 521px;
}

.form-join-us__right {
  max-width: 520px;
}

.form-join-us__input {
  border: 1px solid #DADADA;
  box-sizing: border-box;
  border-radius: 50px;
  font-weight: normal;
  font-size: 16px;
  line-height: 180%;
  /* identical to box height, or 29px */
  color: #787878;
  padding: 15px 23px;
  margin: 0;
  max-height: 60px;

  &:hover {
    border: 1px solid #71A7E2;
    box-sizing: border-box;
  }

  &:focus {
    border: 1px solid #71A7E2;
    box-sizing: border-box;
    background-color: rgba(113, 167, 226, 0.2);
  }

  &:active {
    border: 1px solid #0E3B6C;
  }
}

.form-join-us__option {
  min-width: 208px;
  background: #FFFFFF;
  box-shadow: 0px 0px 19px rgba(0, 0, 0, 0.1);
  border-radius: 20px;
  padding: 20px;

  &:hover {
    background: #3172B9;
    color: white;
  }

  &:focus {
    border: 1px solid #71A7E2;
    box-sizing: border-box;
    background-color: rgba(113, 167, 226, 0.2);
  }

  &:active {
    border: 1px solid #0E3B6C;
  }
}

.form-join-us__textarea {
  grid-column: 3/5;
  grid-row: 1/3;
  width: 100%;
  padding: 19px 28px;
  border: 1px solid #DADADA;
  box-sizing: border-box;
  border-radius: 20px;
  max-width: 520px;
  min-height: 139px;

  &:hover {
    border: 1px solid #71A7E2;
    box-sizing: border-box;
  }
  @media (max-width: 950px) {
    grid-column: 1/3;
    grid-row: 3/5;
    max-width: 100%;
  }
}

.form-join-us__button-group {
  grid-column: 4/5;
  grid-row: 3/4;
  margin-left: 20px;
  @media (max-width: 1200px) {
    grid-column: 3/5;
  }
  @media (max-width: 950px) {
    grid-column: 1/3;
    grid-row: 5/8;
  }
}

.form-join-us__button {
  display: block;
  background: #3172B9;
  border-radius: 50px;
  border: none;
  margin: 0;
  padding: 20px 67px;
  font-weight: 600;
  font-size: 15px;
  line-height: 20px;

  color: #FFFFFF;

  &:hover {
    background: #71A7E2;
  }

  &:focus {
    background: #3172B9;
    border: 1px solid #71A7E2;
    box-sizing: border-box;
  }

  &:active {
    background: #0E3B6C;
  }

  &:disabled {
    background: #C4C4C4;
  }
  @media (max-width:1200px) {
    width: 100%;
  }
}

.form-join-us__checkbox {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 8px;
}

.form-join-us__agreement {
  margin-left: 11px;
  font-weight: normal;
  font-size: 12px;
  line-height: 16px;
  text-align: center;
  color: #787878;
}
