.articles-card {
  display: flex;
  border: 1px solid #E4E4E4;
  box-sizing: border-box;
  border-radius: 5px;
}

.articles-card__img {
  border-radius: 5px 0px 0px 5px;
}

.articles-card__content {
  padding: 18px 30px 18px 30px;

  &:hover {
    background-color: #3172B9;
    cursor: pointer;

    .articles-card__date-pub,
    .articles-card__title,
    .articles-card__text {
      color: #ffffff;
    }
  }

  &:focus {
    background-color: #71A7E2;

    .articles-card__date-pub,
    .articles-card__title,
    .articles-card__text {
      color: #ffffff;
    }
  }

  &:active {
    background-color: #0E3B6C;

    .articles-card__date-pub,
    .articles-card__title,
    .articles-card__text {
      color: #ffffff;
    }
  }
  @media (max-width: 1200px) {
    padding: 18px 15px;
  }
}

.articles-card__date-pub {
  margin: 0 0;
  font-weight: normal;
  font-size: 10px;
  line-height: 150%;
  /* identical to box height, or 15px */
  color: #787878;
}

.articles-card__title {
  font-weight: 600;
  font-size: 20px;
  line-height: 130%;
  /* identical to box height, or 26px */
  color: #1C1C1C;
  padding-bottom: 15px;
  margin: 0 0;
}

.articles-card__text {
  margin: 0 0;
  font-weight: normal;
  font-size: 14px;
  line-height: 150%;
  /* or 21px */
  color: #787878;
}
