.contacts {
  margin: 0 0;
  padding-top: 68px;
  padding-bottom: 160px;
}

.contacts__wrapper {
  display: grid;
  grid-template-columns: repeat(auto-fill, 369px);
  justify-content: space-between;
  margin-top: 111px;
  @media (max-width: 1200px) {
    justify-content: space-evenly;
    gap: 20px;
  }
}

.contacts__title {
  display: block;
  font-weight: 300;
  font-size: 36px;
  line-height: 130%;
  /* identical to box height, or 47px */
  color: #1C1C1C;
  margin-top: 19px;
  margin-bottom: 30px;
}

.contact__title {
  display: block;
  margin-bottom: 31px;
  font-weight: 600;
  font-size: 17px;
  line-height: 180%;
  /* identical to box height, or 31px */
  color: #1C1C1C;
}

.contacts__text {
  margin: 0 0;
}

.contact__svg {
  display: block;
  margin-bottom: 23px;
}
