.footer {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0;
  padding: 39px 0;
}

.footer__logo {
  margin-right: 30px;
}

.footer__logo-copyright {
  font-weight: normal;
  font-size: 12px;
  line-height: 16px;
  color: #787878;
  margin-right: auto;
  padding-right: 50px;
}

.nav-footer__list {
  display: flex;
  padding-left: 0;
}

.nav-footer__item {
  margin-right: 28px;
}

.nav-footer__link {
  font-weight: 600;
  font-size: 15px;
  line-height: 20px;
  color: #787878;
  text-decoration: none;
  cursor: pointer;

  &--active {
    color: #3172B9;
    border-bottom: 2px solid #3172B9;
  }

  &:hover {
    color: #71A7E2;
  }
  &:focus{
    border: 1px solid #71A7E2;
  box-sizing: border-box;
  }
}

.footer__social {
  display: flex;
  margin-left: 28px;
  padding-left: 0;
}

.social__item:not(:last-child) {
  margin-right: 32px;
}
.social__svg{
  &:hover{
  fill: #71A7E2;
  }
  &:focus{
    border: 1px solid #71A7E2;
    box-sizing: border-box;
  }
  &:active{
    fill: #0E3B6C;
  }
}
.social__svg-mail{
  &:hover{
  stroke: #71A7E2;
  }
  &:focus{
    border: 1px solid #71A7E2;
    box-sizing: border-box;
  }
  &:active{
    stroke: #0E3B6C;
  }
}
