.list-reset {
  list-style: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.button {
  padding: 15px 35px;
  background: #3172B9;
  border-radius: 3px;
  font-weight: 600;
  font-size: 15px;
  line-height: 20px;
  color: #FFFFFF;
  text-decoration: none;
}
.button:hover {
  background: #71A7E2;
}
.button:focus {
  background: #3172B9;
  border: 1px solid #71A7E2;
  box-sizing: border-box;
}
.button:active {
  background: #0E3B6C;
}
.button:disabled {
  background: #C4C4C4;
}

.header {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: end;
  padding: 30px 0;
}

.header__logo {
  margin-right: auto;
  margin-top: 10px;
  padding-right: 50px;
}

.main-nav {
  margin: 0 0;
}

.main-nav__list {
  display: flex;
  padding: 0;
  flex-wrap: wrap;
}

.main-nav__item {
  margin-right: 55px;
}
@media (max-width: 1200px) {
  .main-nav__item {
    margin-right: 40px;
  }
}
@media (max-width: 1200px) {
  .main-nav__item {
    margin-right: 30px;
  }
}

.main-nav__link {
  font-weight: 600;
  font-size: 15px;
  line-height: 20px;
  color: #1C1C1C;
  text-decoration: none;
  cursor: pointer;
}
.main-nav__link--active {
  color: #3172B9;
  border-bottom: 2px solid #3172B9;
}
.main-nav__link:hover {
  color: #71A7E2;
}
.main-nav__link:focus {
  border: 1px solid #71A7E2;
  box-sizing: border-box;
}

.sentence {
  background-image: url("../images/contents/first-bg1.webp");
  background-repeat: no-repeat;
  background-position: right;
  max-height: 550px;
  padding-top: 77px;
  padding-bottom: 137px;
}

.sentence__wrapper {
  max-width: 530px;
}

.sentence__title {
  font-weight: 300;
  font-size: 40px;
  line-height: 130%;
  /* or 52px */
  color: #1C1C1C;
  margin-bottom: 26px;
}

.sentence__text {
  font-weight: normal;
  font-size: 16px;
  line-height: 155%;
  /* or 25px */
  color: #787878;
  margin-bottom: 34px;
  margin-right: 40px;
}

.sentence-button__text {
  font-weight: normal;
  font-size: 11px;
  line-height: 155%;
  /* identical to box height, or 17px */
  text-transform: uppercase;
  color: #787878;
  margin-left: 9px;
}

.sentence-button__descryption {
  margin-top: 29px;
}

.sentence-button__wrapper {
  display: inline-block;
  text-align: center;
  margin-top: 19px;
}

.sentence__button {
  padding: 19px 58px;
}

.sentence-button__ssl {
  position: relative;
  top: 4px;
}

.services {
  padding-top: 85px;
  padding-bottom: 158px;
}

.services__title,
.services__text {
  text-align: center;
}

.services__title {
  padding-bottom: 17px;
  margin: 0 0;
}

.services__text {
  margin: 0 0;
  padding-bottom: 54px;
  font-weight: normal;
  font-size: 16px;
  line-height: 22px;
  /* identical to box height */
  color: #787878;
}

.services__wrapper-card {
  display: grid;
  gap: 30px;
  grid-template-columns: repeat(auto-fill, 270px);
  justify-content: center;
}

.card-advantage {
  border: 1px solid #E4E4E4;
  box-sizing: border-box;
  border-radius: 5px;
  padding: 62px 30px;
  max-width: 270px;
  text-align: center;
}
.card-advantage:hover {
  background-color: #3172B9;
  cursor: pointer;
}
.card-advantage:hover .card-advantage__title,
.card-advantage:hover .card-advantage__text, .card-advantage:hover .card-advantage__svg {
  color: #ffffff;
  fill: #ffffff;
}
@media (max-width: 1200px) {
  .card-advantage {
    padding: 30px 15px;
  }
}

.card-advantage__title {
  padding-top: 27px;
  padding-bottom: 13px;
  margin: 0 0;
  font-weight: 600;
  font-size: 20px;
  line-height: 130%;
  /* identical to box height, or 26px */
  color: #1C1C1C;
}

.card-advantage__text {
  margin: 0 0;
  font-weight: normal;
  font-size: 14px;
  line-height: 150%;
  /* or 21px */
  color: #787878;
}

.articles {
  margin: 0 0;
  padding-top: 87px;
  padding-bottom: 160px;
}

.articles__title {
  font-weight: 300;
  font-size: 36px;
  line-height: 130%;
  /* identical to box height, or 47px */
  color: #1C1C1C;
  padding-bottom: 42px;
  margin: 0 0;
}

.articles__wrapper {
  display: grid;
  gap: 30px;
  grid-template-columns: repeat(auto-fill, 570px);
  justify-content: center;
}

.articles__link {
  text-decoration: none;
}

.articles-card {
  display: flex;
  border: 1px solid #E4E4E4;
  box-sizing: border-box;
  border-radius: 5px;
}

.articles-card__img {
  border-radius: 5px 0px 0px 5px;
}

.articles-card__content {
  padding: 18px 30px 18px 30px;
}
.articles-card__content:hover {
  background-color: #3172B9;
  cursor: pointer;
}
.articles-card__content:hover .articles-card__date-pub,
.articles-card__content:hover .articles-card__title,
.articles-card__content:hover .articles-card__text {
  color: #ffffff;
}
.articles-card__content:focus {
  background-color: #71A7E2;
}
.articles-card__content:focus .articles-card__date-pub,
.articles-card__content:focus .articles-card__title,
.articles-card__content:focus .articles-card__text {
  color: #ffffff;
}
.articles-card__content:active {
  background-color: #0E3B6C;
}
.articles-card__content:active .articles-card__date-pub,
.articles-card__content:active .articles-card__title,
.articles-card__content:active .articles-card__text {
  color: #ffffff;
}
@media (max-width: 1200px) {
  .articles-card__content {
    padding: 18px 15px;
  }
}

.articles-card__date-pub {
  margin: 0 0;
  font-weight: normal;
  font-size: 10px;
  line-height: 150%;
  /* identical to box height, or 15px */
  color: #787878;
}

.articles-card__title {
  font-weight: 600;
  font-size: 20px;
  line-height: 130%;
  /* identical to box height, or 26px */
  color: #1C1C1C;
  padding-bottom: 15px;
  margin: 0 0;
}

.articles-card__text {
  margin: 0 0;
  font-weight: normal;
  font-size: 14px;
  line-height: 150%;
  /* or 21px */
  color: #787878;
}

.clients {
  margin: 0 0;
  padding-top: 88px;
  padding-bottom: 160px;
}

.clients__title {
  margin: 0 0;
  padding-bottom: 42px;
  font-weight: 300;
  font-size: 36px;
  line-height: 130%;
  /* identical to box height, or 47px */
  color: #1C1C1C;
}

.clients__wrapper-card {
  display: grid;
  gap: 30px;
  grid-template-columns: repeat(auto-fill, 370px);
  justify-content: center;
}

.client-card {
  margin: 0 0;
  border: 1px solid #E4E4E4;
  box-sizing: border-box;
  border-radius: 5px;
  padding-bottom: 9px;
  padding-top: 51px;
  padding-left: 36px;
  padding-right: 37px;
}

.clients-card__text {
  font-weight: normal;
  font-size: 14px;
  line-height: 180%;
  /* or 25px */
  color: #787878;
  margin: 0 0;
  margin-bottom: 30px;
}

.client-card__content {
  display: flex;
  align-items: center;
  margin: 0 0;
  max-width: 297px;
  border-top: 1px solid #DADADA;
  padding-top: 28px;
}

.client-card__img {
  border-radius: 50%;
  border: 2px solid #3172B9;
  max-width: 67px;
  max-height: 67px;
  margin-right: 17px;
  position: relative;
  top: -14px;
  left: 2px;
}

.clients-card__client-name {
  font-weight: 600;
  font-size: 17px;
  line-height: 180%;
  /* identical to box height, or 31px */
  color: #1C1C1C;
  margin: 0 0;
}

.contacts {
  margin: 0 0;
  padding-top: 68px;
  padding-bottom: 160px;
}

.contacts__wrapper {
  display: grid;
  grid-template-columns: repeat(auto-fill, 369px);
  justify-content: space-between;
  margin-top: 111px;
}
@media (max-width: 1200px) {
  .contacts__wrapper {
    justify-content: space-evenly;
    gap: 20px;
  }
}

.contacts__title {
  display: block;
  font-weight: 300;
  font-size: 36px;
  line-height: 130%;
  /* identical to box height, or 47px */
  color: #1C1C1C;
  margin-top: 19px;
  margin-bottom: 30px;
}

.contact__title {
  display: block;
  margin-bottom: 31px;
  font-weight: 600;
  font-size: 17px;
  line-height: 180%;
  /* identical to box height, or 31px */
  color: #1C1C1C;
}

.contacts__text {
  margin: 0 0;
}

.contact__svg {
  display: block;
  margin-bottom: 23px;
}

.contacts__text {
  font-weight: normal;
  font-size: 14px;
  line-height: 180%;
  /* or 25px */
  color: #787878;
  text-decoration: none;
}
.contacts__text:hover {
  color: #71A7E2;
}
.contacts__text:focus {
  border: 1px solid #71A7E2;
  box-sizing: border-box;
}
.contacts__text:active {
  color: #0E3B6C;
}

.contacts__content {
  max-width: 220px;
}

.join-us {
  margin: 0;
  padding-top: 87px;
  padding-bottom: 122px;
}

.join-us__title {
  margin: 0;
  font-weight: 300;
  font-size: 36px;
  line-height: 130%;
  /* identical to box height, or 47px */
  text-align: center;
  color: #1C1C1C;
  margin-bottom: 22px;
}

.join-us__text {
  margin: 0;
  font-weight: normal;
  font-size: 16px;
  line-height: 22px;
  /* identical to box height */
  text-align: center;
  color: #787878;
  margin-bottom: 30px;
}

/* .form-join-us {

  display: grid;
  gap: 19px;
  grid-template-columns: repeat(auto-fill, 251px);
  justify-content: center;
} */
.form-join-us {
  display: grid;
  grid-template-columns: 250px 250px 250px 250px;
  gap: 19px;
  justify-content: center;
}
@media (max-width: 1200px) {
  .form-join-us {
    grid-template-columns: 25% 25% 25% 25%;
    margin: 0 100px;
  }
}
@media (max-width: 950px) {
  .form-join-us {
    grid-template-columns: 50% 50%;
    margin: 0 50px;
  }
}

.form-join-us__left {
  max-width: 521px;
}

.form-join-us__right {
  max-width: 520px;
}

.form-join-us__input {
  border: 1px solid #DADADA;
  box-sizing: border-box;
  border-radius: 50px;
  font-weight: normal;
  font-size: 16px;
  line-height: 180%;
  /* identical to box height, or 29px */
  color: #787878;
  padding: 15px 23px;
  margin: 0;
  max-height: 60px;
}
.form-join-us__input:hover {
  border: 1px solid #71A7E2;
  box-sizing: border-box;
}
.form-join-us__input:focus {
  border: 1px solid #71A7E2;
  box-sizing: border-box;
  background-color: rgba(113, 167, 226, 0.2);
}
.form-join-us__input:active {
  border: 1px solid #0E3B6C;
}

.form-join-us__option {
  min-width: 208px;
  background: #FFFFFF;
  box-shadow: 0px 0px 19px rgba(0, 0, 0, 0.1);
  border-radius: 20px;
  padding: 20px;
}
.form-join-us__option:hover {
  background: #3172B9;
  color: white;
}
.form-join-us__option:focus {
  border: 1px solid #71A7E2;
  box-sizing: border-box;
  background-color: rgba(113, 167, 226, 0.2);
}
.form-join-us__option:active {
  border: 1px solid #0E3B6C;
}

.form-join-us__textarea {
  grid-column: 3/5;
  grid-row: 1/3;
  width: 100%;
  padding: 19px 28px;
  border: 1px solid #DADADA;
  box-sizing: border-box;
